<template lang="pug">
  .full-width
    h1 Cards

    <div>
      <h-card>
        <h-card-media img="img/Nallah_Sindh.jpg"/>
        <h-card-header text="River"  desc="Beautifull place" />
        <h-card-text desc="A river in the Ganderbal district of Jammu and Kashmir state of India." />
      </h-card>

    </div>

    .boxshadow
      <pre v-highlightjs="card1">
        <code class="html">
        </code>
      </pre>

    <div>
      <h-card>
        <h-card-media img="img/Nallah_Sindh.jpg"/>
        <h-card-header avatar="/avatar/Kostenlose.jpg"  text="River"  desc="Beautifull place" />
        <h-card-text desc="A river in the Ganderbal district of Jammu and Kashmir state of India." />
        <h-card-actions>
          <h-card-buttons>
            <h-btn textbutton text="explore"/>
            <h-btn textbutton text="share"/>
          </h-card-buttons>
          <h-card-icons>
            <h-btn textbutton> <h-fa-icon :icon="['fas','heart']"/> </h-btn>
            <h-btn textbutton> <h-fa-icon :icon="['fas','share-alt']"/> </h-btn>
          </h-card-icons>
        </h-card-actions>
      </h-card>
    </div>

    .boxshadow
      <pre v-highlightjs="card2">
        <code class="html">
        </code>
      </pre>

    h1  Card Scannable

    .flex.flex-wrap
      <h-card style="width:256px">
       <h-card-media img="img/90minute.png"/>
       <h-card-header  text="90th minute" desc="4.31 MB"/>
      </h-card>

      <h-card style="width:256px">
       <h-card-media img="/img/breakpoint.png"/>
       <h-card-header text="Break point"  desc="5.12 MB"/>
      </h-card>

    .flex.flex-wrap
      <h-card style="width:256px">
       <h-card-media img="img/matchday.png"/>
       <h-card-header text="Matchday"  desc="2.18 MB"/>
      </h-card>

      <h-card style="width:256px">
       <h-card-media img="img/bluerun.png"/>
       <h-card-header text="Blue run"  desc="3.33 MB"/>
      </h-card>

    <pre v-highlightjs="cardScannable">
      <code class="html">
      </code>
    </pre>

    h1  Card Distinction

    div.flex.flex-justify-center.boxshadow.h-ma-md(style="width:380px;")
      .flex.flex-column.flex-wrap
        div.h-ma-md(style="width:360px; height: 200px;")
          <h-card>
            <h-card-media style="width:360px; height: 200px;"  img="img/90minute.png">
              <h-card-media-text class="flex-items-end" text="90th minute" desc="4.31 MB"/>
            </h-card-media>
          </h-card>

        div.h-ma-md.flex.flex-items-center(style="width:360px; height: 200px;")
          <h-card>
            <h-card-media style="width:170px; height: 128px;" img="img/bluerun.png">
              <h-card-media-text class="flex-items-end" text="Blue run" desc="3.33 MB"/>
            </h-card-media>
          </h-card>

          <h-card>
            <h-card-media style="width:160px; height: 192px;"  img="img/breakpoint.png">
              <h-card-media-text class="flex-items-center" text="Break point"  desc="5.12 MB"/>
            </h-card-media>
          </h-card>

        div.h-ma-md(style="width:360px; height: 200px;")
          <h-card>
            <h-card-media style="width:360px; height: 200px;" img="img/matchday.png">
              <h-card-media-text class="flex-items-center flex-justify-center" text="Match day" desc="2.18 MB"/>
            </h-card-media>
          </h-card>

    <pre v-highlightjs="cardDistinction">
      <code class="html">
      </code>
    </pre>

    <h1 class="text-primary"> Card Header Help</h1>
    <h2 class="text-primary"> Vue Properties</h2>
    <hr>

    <div class="flex">
      <div>
        <h3>Name</h3>
        <div>icon</div>
        <div>avatar</div>
        <div>text</div>
        <div>desc</div>
      </div>
      <div class="h-pl-md">
        <h3>Type</h3>
        <div>Array</div>
        <div>String</div>
        <div>String</div>
        <div>String</div>
      </div>
      <div class="h-pl-md">
        <h3 >Description</h3>
        <div>Sets the icon for header</div>
        <div>Sets the avatar for the header</div>
        <div>Sets the text for header</div>
        <div>Sets the description for the header</div>
      </div>
    </div>

    <h1 class="text-primary"> Card Media Help</h1>
    <h2 class="text-primary"> Vue Properties</h2>
    <hr>

    <div class="flex">
      <div>
        <h3>Name</h3>
        <div>img</div>
      </div>
      <div class="h-pl-md">
        <h3>Type</h3>
        <div>String</div>
      </div>
      <div class="h-pl-md">
        <h3 >Description</h3>
        <div>Sets the img for media</div>
      </div>
    </div>

    <h1 class="text-primary"> Card Media Text Help</h1>
    <h2 class="text-primary"> Vue Properties</h2>
    <hr>

    <div class="flex">
      <div>
        <h3>Name</h3>
        <div>text</div>
        <div>desc</div>
      </div>
      <div class="h-pl-md">
        <h3>Type</h3>
        <div>String</div>
        <div>String</div>
      </div>
      <div class="h-pl-md">
        <h3 >Description</h3>
        <div>Sets the text for the image in <b>h-card-media</b></div>
        <div>Sets the description for the image in <b>h-card-media</b></div>
      </div>
    </div>

</template>

<script>

export default {
